

.slider {
	min-height: 500px;
	background-color: #fbfcff;

}

.slider__wrapper {
	@media (min-width: 300px) and (max-width: 900px) {
		margin-bottom: 80px;
		border-bottom: 2px solid #f7040e;
	}

	@media (min-width: 900px) {
		display: flex;
		height: 450px;
		margin-bottom: 50px;
		margin-left: 50px;
		margin-right: 50px;
		box-shadow: 0px 2px 13px 0px rgba(0,0,0,0.75);
	}
}


.slide2,
.slide4 {
	flex-direction: row-reverse;
}

.slider__img1 {
		background-image: url("../img/im-1.jpg");
		background-size: 100%;
		background-repeat: no-repeat;
	@media (min-width: 300px) and (max-width: 900px) {
		width: 100vw;
		height: 250px;
	}

	@media (min-width: 900px) {
		width: 50%;
		height: 450px;
	}
}

