


.info {

	max-width: 100vw;
	min-height: 400px;
	margin-bottom: 100px;

}

.info__list {
	padding-left: 0;
	width: 90%;
	margin: auto;
	list-style: none;
}

.info__item {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.info__b {
	font-family: "arial";
	opacity: 1;
	text-align: left;

	@media (min-width: 900px) {
		padding-left: 50px;
		font-size: 24px;
		margin-right: 250px;

	}
}

.info__text {
	font-family: "Arial";
	opacity: 1;

	@media (min-width: 300px) and (max-width: 900px) {
		text-align: right;
	}

	@media (min-width: 900px) {
		width: 550px;
		font-size: 20px;

	}
}

.info__item--dark {
	background-color: #9d8d80;
}