

.contacts {


	@media (min-width: 900px) {

		padding-left: 20px;
		
		background-image: url("../img/logo.png");
		background-size: 250px 240px;
		background-position: center right;
		background-repeat: no-repeat;
	}
}

.contacts__title {
	font-family: "Arial";
	text-align: center;
	color: black;
	margin-top: 0;
}

.contacts__item {
	display: flex;
	flex-direction: column;
	margin-bottom: 25px;
}

.contacts__text {
	font-family: "Arial";
	color: black;
	margin: 0;
}

.contacts__name {
	font-family: "Arial";
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 10px;
}

.contacts__link {
	width: 150px;
	font-family: "arial";
	text-decoration: none;
	color: black;

	&:hover {
		color: $hover;
	}
}

.contacts__license {
	text-decoration: none;
	text-align: right;
	color: black;
	margin: 0;

	&:hover {
		color: $hover;
	} 
}