.process {
	@media (min-width: 700px) {
		margin-left: 50px;
		margin-right: 50px;
	}
}
.process__list {
	@media (min-width: 300px) and (max-width: 700px) {
		width: 100vw;
	}
	padding-left: 0;
	display: flex;
	flex-wrap: wrap;
	list-style: none;

}
.process__item {

	@media (min-width: 300px) and (max-width: 700px) {
		width: 100%;
		position: relative;
		display: flex;
		flex-direction: column;
		box-shadow: 0px 5px 10px -1px rgba(0,0,0,0.33);
	}

	@media (min-width: 700px) {
		width: 50%;
		height: 400px;
		position: relative;
		display: flex;
		box-shadow: 0px 5px 10px -1px rgba(0,0,0,0.33);
	}
}

.process__item1 {
	@media (min-width: 700px) {
		background-image: url("../img/img-7.jpg");
		background-size: 100%;
		background-position: 0 0;
	}
}

.process__block {
	z-index: 35;
	@media (min-width: 700px) {
		width: 100%;
	}
}
.proect__img1 {
	

	@media (max-width: 700px) {
		width: 100%;
	}

	@media (min-width: 700px) {
		width: 50%;
	}
}
.process__title {
	font-family: "arial";
	z-index: 5;

	@media (min-width: 300px) and (max-width: 700px) {
		font-size: 35px;
	}

	@media (min-width: 700px) {
		font-size: 35px;
		margin-top: 0;
	}

}
