    .header__burger:before, .header__burger:after {
      content: "";
      background-color: #fff;
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0; } }

.header__burger:before {
  top: 4px; }

.header__burger:after {
  bottom: 2px; }

